import { Block, Selection, Point, Controller } from '@ali/4ever-cangjie';

export default function generateRangesFromTableSelection(controller: Controller, tableSelections) {
  const { document } = controller.value;
  const ranges: Selection[] = [];
  if (!tableSelections) return undefined;
  tableSelections.forEach(tableSelection => {
    if (!tableSelection) {
      return undefined;
    }
    const {
      key, startRowIndex, startColIndex, endRowIndex, endColIndex,
    } = tableSelection;

    const table = document.getNode(key) as Block;
    if (!table) {
      return [];
    }

    for (let rowIndex = startRowIndex; rowIndex <= endRowIndex; rowIndex += 1) {
      const row = table.nodes[rowIndex] as Block;
      if (!row) {
        return undefined;
      }

      for (let colIndex = startColIndex; colIndex <= endColIndex; colIndex += 1) {
        const cell = row.nodes[colIndex];
        if (!cell) {
          continue;
        }

        const anchorText = cell.getFirstText();
        const focusText = cell.getLastText();
        if (!anchorText || !focusText) {
          break;
        }
        ranges.push(
          Selection.create({
            anchor: Point.create({
              key: anchorText.key,
              // @ts-ignore
              offset: 0,
            }),
            focus: Point.create({
              key: focusText.key,
              // @ts-ignore
              offset: focusText.text.length,
            }),
          }),
        );
      }
    }

    return undefined;
  });
  return ranges;
}
